import React from 'react'
import Link from 'gatsby-link'
import styled from 'styled-components'
import breakpoint from "styled-components-breakpoint"

import { translate } from "react-i18next"

import i18n from "../utils/i18n.js"

const Nav = styled.nav`
  text-align: center;
  display: flex;
  flex-flow: row;
  justify-content: space-around;
  align-items: center;
  ${props => breakpoint('xs', props.theme.breakpoints)`
    width: 275px;
  `}
  ${props => breakpoint('sm', props.theme.breakpoints)`
    width: 400px;
  `}

`

const NavLink = styled.button`
  color: ${props => props.theme.color.p};
  background-color: white;
  border: 0;
  border-radius: .1rem;
  font-size: 1rem;
  padding: 1rem 1rem;
  transition: ${props => props.theme.effects.transition};
  &:hover {
    background-color: ${props => props.theme.color.s};
    box-shadow: ${props => props.theme.effects.shadow[0]};
    cursor: pointer;
  }
  &:focus{
    outline: none;
  }
  ${props => breakpoint('xs', props.theme.breakpoints)`
    padding: .5rem .5rem;
  `}
  ${props => breakpoint('sm', props.theme.breakpoints)`
  font-size: 1.25rem;
  `}
  &:active{
    background-color: ${props => props.theme.color.s};
    box-shadow: ${props => props.theme.effects.shadow[0]};
    cursor: pointer;
  }
`

const Navigation = ({data, t}) => (
    <Nav>
          <Link
            to={"/about/"}
            style={{
              display: 'block',
              textDecoration: 'none',
              transition: '250ms all ease'
            }}
          >
          <NavLink>{ t("nav.about") }</NavLink>
          </Link>
          <Link
            to={"/education/"}
            style={{
              display: 'block',
              textDecoration: 'none',
              transition: '250ms all ease'
            }}
          >
          <NavLink>{ t("nav.education") }</NavLink>
          </Link>
          <Link
            to={"/contact/"}
            style={{
              display: 'block',
              textDecoration: 'none',
              transition: '250ms all ease'
            }}
          >
          <NavLink>{ t("nav.contact") }</NavLink>
          </Link>
    </Nav>
)

export default translate()(Navigation)



