import React from 'react';
import Link from "gatsby-link"
import Svg from "react-svg-inline"
import styled from 'styled-components'
import breakpoint from 'styled-components-breakpoint'

import logo from "../assets/img/NSLB-horizontal.svg"

const Img = styled.img`
  min-width: 10rem;
  max-width: 12rem;
  height: 4rem;
  margin-bottom: 0;
  transition: ${props => props.theme.effects.transition};
  &:hover{
    opacity: 0.7;
  }
  ${props => breakpoint('sm', props.theme.breakpoints)`
    min-width: 12rem;
    max-width: 16rem;
    height: 4rem;
  `}
  ${props => breakpoint('lg', props.theme.breakpoints)`
    min-width: 16rem;
    max-width: 18rem;
    height: 8rem;
  `}
`

const Logo = ()=>{
  console.log(logo)
  return(
    <Link style={{display: "block", maxWidth: "20rem", maxHeight: "7rem"}} to="/">
      <Img src={logo}/>
    </Link>
  )
}

export default Logo



