import React from 'react';
import ReactDOM from 'react-dom';
import styled from "styled-components"
import { translate } from 'react-i18next';
import {Button} from  '../utils/styled-components.js'

import i18n from '../utils/i18n.js'; // initialized i18next instance using reactI18nextModule

const LanguageWrapper = styled.div`
`
const LButton = Button.extend`
  font-size: .65rem;
  padding: .25rem .5rem;
  margin: .1rem;
`

const Language = ()=>{
  return (
      <LanguageWrapper>
        <LButton onClick={ () => i18n.changeLanguage('en')}>English</LButton>
        <LButton onClick={ () => i18n.changeLanguage('es')}>Español</LButton>
      </LanguageWrapper>
    )
}

export default Language



